import React, { useRef } from "react";
import main7Styles from "../../assets/pagesStyle/main/main6.module.css";
import right from "../../assets/images/rightArrow.svg";
import left from "../../assets/images/leftArrow.svg";
import Nav from "../../components/Nav";
import { Swiper, SwiperSlide } from "swiper/react";
import companyData from "../../data/companyData";
// Import Swiper styles
import "swiper/css";
import "swiper/css/scrollbar";
import { Autoplay, Scrollbar } from "swiper/modules";

const Main7 = () => {
  const swiperRef = useRef(null);
  const slideNext = () => {
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slideNext();
    }
  };

  const slidePrev = () => {
    if (swiperRef.current && swiperRef.current.swiper) {
      swiperRef.current.swiper.slidePrev();
    }
  };

  // 파트너 로고 배열

  return (
    <>
      <Nav />
      <div className={main7Styles.main6}>
        <div className={main7Styles.main6Box}>
          <div className={main7Styles.txtBox}>
            <h1>Partner company</h1>
            <h2>
              와이시스템즈와 같이 <br />
              미래를 만들어가는 기업입니다.
            </h2>
            <div className={main7Styles.btn}>
              <img src={left} alt="leftArrow" onClick={slidePrev} />
              <img src={right} alt="rightArrow" onClick={slideNext} />
            </div>
          </div>
          {/* 스와이퍼 부분 */}
          <Swiper
            ref={swiperRef}
            slidesPerView={1}
            spaceBetween={10}
            loop={true}
            breakpoints={{
              "@0.00": {
                slidesPerView: 1,
                spaceBetween: 10,
              },
              "@0.75": {
                slidesPerView: 2,
                spaceBetween: 20,
              },
              "@1.00": {
                slidesPerView: 3,
                spaceBetween: 40,
              },
              "@1.50": {
                slidesPerView: 4,
                spaceBetween: 50,
              },
            }}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            modules={[Autoplay, Scrollbar]}
            className="main6Swiper"
          >
            {companyData.map((company, index) => (
              <SwiperSlide key={index} className={main7Styles.partner}>
                <div className={main7Styles.logo}>
                  <img src={company.img} alt={company.img} />
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </>
  );
};

export default Main7;
