import React from "react";
import { useParams } from "react-router-dom";
import downData from "../data/downData";
import downStyles from "../assets/pagesStyle/promote/download.module.css";

const DownDetail = () => {
  const { id } = useParams();
  const downItem = downData.find((item) => item.id === parseInt(id));

  if (!downItem) {
    return <div>News item not found</div>;
  }

  return (
    <div className={downStyles.downDetail}>
      <h1>{downItem.title}</h1>
      <img src={downItem.img} alt={downItem.title} />
      <p>{downItem.content}</p>
    </div>
  );
};

export default DownDetail;
