import React from "react";
import { useParams } from "react-router-dom";
import techSupport from "../data/techSupport";
import techStyles from "../assets/pagesStyle/customer/techsupport.module.css";

const TechDetail = () => {
  const { id } = useParams();
  const techItem = techSupport.find((item) => item.id === parseInt(id));

  if (!techItem) {
    return <div>tech item not found</div>;
  }

  return (
    <div className={techStyles.techDetail}>
      <h1>{techItem.title}</h1>
      <img src={techItem.img} alt={techItem.title} />
      <p>{techItem.content}</p>
    </div>
  );
};

export default TechDetail;
