import React, { useState } from "react";
import techStyles from "../../assets/pagesStyle/customer/techsupport.module.css";
import Footer from "../footer/Footer";
import techSupport from "../../data/techSupport";
import { Link } from "react-router-dom";

const Techsupport = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10; // Adjust this number based on how many items you want per page

  const totalPages = Math.ceil(techSupport.length / itemsPerPage);

  const handleClick = (pageNum) => {
    setCurrentPage(pageNum);
  };

  const currentItems = techSupport.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  return (
    <>
      <div className={techStyles.tech}>
        <div className={techStyles.techBox}>
          <div className={techStyles.txtBox}>
            <h1>기술지원</h1>
            <p>내용을 입력하세요.</p>
          </div>
          <div className={techStyles.supportBox}>
            <div className={techStyles.supportTop}>
              <h2>번호</h2>
              <h2>제목</h2>
              <h2>작성일</h2>
            </div>
            {currentItems.map((item, i) => (
              <div key={i} className={techStyles.support}>
                <p className={techStyles.supportNum}>{item.id}</p>
                <Link to={`/customer/support/${item.id}`}>
                  <p className={techStyles.supportTitle}>{item.title}</p>
                </Link>
                <p>{item.date}</p>
              </div>
            ))}
          </div>
        </div>
        <div className={techStyles.pagination}>
          {Array.from({ length: totalPages }, (_, i) => (
            <button
              key={i}
              onClick={() => handleClick(i + 1)}
              className={currentPage === i + 1 ? techStyles.activePage : ""}
            >
              {i + 1}
            </button>
          ))}
        </div>
      </div>
      <Footer />
    </>
  );
};

export default Techsupport;
